
.cart {
  width: 600px;
}
.checkout-table {
  width: 100%;
}
.checkout-table th {
  text-align: left;
  padding: 15px 0px;
  border-bottom: 1px solid #aaa;
}
.checkout-table td {
  padding: 8px 0px;
}
.checkout-button {
  float: right;
  width: 120px;
  height: 40px;
  margin-top: 20px;
}
.total td {
  border-top: 1px solid #aaa;
  padding-top: 10px;
}



